import { Injectable } from '@angular/core';
import { ConstantsService } from 'src/app/services/constants.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Observable, throwError } from 'rxjs';
import { AngularFireDatabase } from '@angular/fire/database';

@Injectable({
  providedIn: 'root'
})
export class CategoryManagementService {
  heightTable = "775";
  columnsTable = [
    { title: "id", field: "id", align: "left", width: 50 },
    { title: "Title", field: "title", align: "left", width: 200 },
    { title: "Value", field: "value", align: "left", width: 100 },
    { title: "Active", field: "active", align: "left", width: 100 }
  ];
  constructor(private constantsService: ConstantsService,
    public db: AngularFireDatabase,
    private utilsService: UtilsService) {

  }

  importToDB(dataInsert: any) {
    return Observable.create(observer => {
      let addNewData = this.db.object("/");
      let dataDelete = {};
      dataDelete[`Category/`] = null;
      addNewData.update(dataDelete).then(result => {
        addNewData.update(dataInsert).then(result => {
          observer.next(true);
          observer.complete();
        }).catch(error => {
          throwError(error);
        });
      }).catch(error => {
        throwError(error);
      });
    },
      error => {
        throwError(error);
      });
  }
}
